
/* This is the coffee image itself */
.coffee-img{
   margin-top: 20px; 
    border: 2px solid #e3b098; 
    align-content: center; 
    box-shadow: 0 5px 10px -5px;
    max-width: 100%;
}

/* This is the coffee button under the image  */
.coffee-btn{
    border: 1px solid #e49b7f; 
    background: #e3b098; 
    margin-top: 20px; 
    color: #3a1707; 
}